@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
body{
    margin: 0;
}
body *{
    font-family: 'Nunito',sans-serif;
    transition:all ease-in-out 400ms;
}
.container{
    margin: 0 auto;
    max-width: 1370px;
}
.popular,.services,.footer,.item-list,.filter-panel,.room-info{
    padding: 80px 0;
}
.services,.footer{
    background-color: #cfcfcf;
    background-color: #282d32;
    color:rgb(255, 255, 255);
}
.header{
    max-height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 20px;
    height: 60px;
    padding: 0 20px;
}
.header *{
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.header-logo{
    margin: auto 0;
    text-align: center;
}
.menu{
    display: -webkit-flex;
    display: flex;
    padding: 0;
    margin: auto 0;
}
.menu-item{
    list-style-type: none;
    text-decoration: none;
    padding:0 20px;
    cursor: pointer;
    text-align: center;
}
.menu-item:hover{
    background-color: black;
    color:white;
}
.menu-item:hover .link{
    color:white;
}
.menu-right-panel{
    margin: auto 0;
}
.menu-right-panel i{
    padding: 0 20px;
}
.header .link{
    color:black;
}
.demo{
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    min-height: 700px;
}
.demo-info{
    padding: 50px 70px;
    background-color: rgba(0, 0, 0, 0.5);
    
}
.demo-info *{
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 20px;
    margin-bottom: 20px;
    color:white;
}
.demo-info h2{
    font-size: 64px;
    font-weight: 800;
}
.demo-info h4{
    font-size: 20px;
}
.demo-info-line{
    width: 20%;
}
.demo-info-line{
    border-bottom: dashed 6px #af9a7d;;
}
.link{
    text-decoration: none;
}

.gold-btn:hover{
    background-color: transparent;
    color:#af9a7d;
}
.gold-btn{
    font-size: 20px;
    padding-top:10px;
    padding-bottom: 10px;
    background-color: #af9a7d;
    color:black;
    font-weight: 200;
    border: none;
    cursor: pointer;
    border: solid 2px #af9a7d;
    box-sizing: border-box;
    z-index: 2;
}
.services-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:5vw;
}
.services-item{
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}
.services-item *{
    margin:10px 0;
}
.services-item i{
    font-size: 46px;
    color:#af9a7d;
}
.services-item p{
    font-size: 16px;
}
.services-item h5{
    font-size: 20px;
    font-weight: 900;
}
.section-title{
    margin-bottom: 60px;
}
.section-title h4{
    font-size: 32px;
    text-align: center;
    margin:0 0 15px;
}
.section-title-underline{
    width: 10%;
    height: 6px;
    background-color:#af9a7d;
    margin: 0 auto;
}
.popular-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:5vw;
}
.item{
    height: 260px;
    background-position: center;
    background-size: cover;
    position: relative;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.room-name{
    display: block;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    padding: 8px 0;
    background-color: #cfcfcfa8;
    color:rgb(0, 0, 0);
    font-size: 16px;
    font-weight: 800;
}
.room-coast{
    position: absolute;
    padding: 10px 20px;
    background-color: rgba(0, 0, 0, 0.5);
    color:white;
    border-radius:0 0 30px;
    left:0;
    top: 0;
}
.item:hover .room-name{
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.item:hover .room-coast{
    -webkit-transform: translateX(600%);
            transform: translateX(600%);
    border-radius:30px 30px;
}
.item:hover{
        background-size: cover;
}
.item:hover .opacity-block{
    background-color: rgba(0, 0, 0, 0.5);
}
.opacity-block{
    background-color: rgba(0, 0, 0, 0);
    width: 100%;
    height: 100%;
    position:absolute;
}
.item .gold-btn{
    opacity: 0;
}
.item:hover .gold-btn{
    opacity: 1;
}
.link-to-item{
    z-index: 100;
}
.footer-content{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    
}
.item-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:5vw
}
.select-title{
    display: inline-block;
    font-weight: 800;
    margin-bottom: 5px;
}
.select-container input{
    margin-right: 5px;
}
.select-container input,.select-container select{
    border:solid 1px rgba(0,0,0,0.3);
    border-radius: 10px;
    outline:none;
    padding: 10px 20px;
}
.select-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.range-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.select-container div,.range-container div{
    width: 100%;
}
.filter{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    
}
.price-range-selector{
    width: 80%;
}
.square-selector{
    width: 30%;
    box-sizing:content-box;
}
.stars-count,.persons{
    width: 60%;
}
.extended-options input{
    margin-right: 10px;
}
.room-images{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:5vw;
}
.room-image{
    width: 100%;
    height: 260px;
    background-size: cover;
    background-repeat: no-repeat;
}
.room-main-info{
    display: -webkit-flex;
    display: flex;
}
.details{
    width: 50%;
}
.room-info h3{
    font-size: 26px;
}
.room-info p,.room-info li{
    font-size: 20px;
}
.room-info ul{
    margin: 0;
    padding: 0;
}
.room-info ul li{
    list-style: none;
    margin: 8px 0;
}
.details p{
    margin: 0;
}
.key-info{
    margin-left: 30px;
}
.extra-section{
    margin: 30px 0;
}
.room-info li::before{
    content:"- ";
}
.room-extras-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:1vw
}
