.room-images{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap:5vw;
}
.room-image{
    width: 100%;
    height: 260px;
    background-size: cover;
    background-repeat: no-repeat;
}
.room-main-info{
    display: flex;
}
.details{
    width: 50%;
}
.room-info h3{
    font-size: 26px;
}
.room-info p,.room-info li{
    font-size: 20px;
}
.room-info ul{
    margin: 0;
    padding: 0;
}
.room-info ul li{
    list-style: none;
    margin: 8px 0;
}
.details p{
    margin: 0;
}
.key-info{
    margin-left: 30px;
}
.extra-section{
    margin: 30px 0;
}
.room-info li::before{
    content:"- ";
}
.room-extras-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap:1vw
}